form {
    width: 90%;
    max-width: 300px;
    /* background-color: rgb(24, 87, 18); */
    margin: 0 auto;
    padding: 33px 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

label {
    text-align: center;
    font-weight: bold;
    font-size: 20px;
    color: white;
    margin-bottom: 25px;
}

textarea {
    background-color: aliceblue;
    border: 2px solid rgb(50, 158, 68);
    border-radius: 2px;
    box-shadow: 0px 5px 50px rgb(104, 211, 90);
    font-size: 20px;
    height: 96px;
    width: calc(100% - 30px);
    padding: 12px;
}

.Formulario-contenedor {
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.Formulario-agregar {
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color: white;
    background-color: rgb(118, 228, 67);
    font-weight: 400;
    width: 120px;
    height: 48px;
    border-radius: 2px;
    font-family: sans-serif;

}

.Formulario-cancelar {
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color: white;
    background-color: red;
    font-weight: 400;
    width: 120px;
    height: 48px;
    border-radius: 2px;
    font-family: sans-serif;

}