.CreaTareas {
    border-radius: 5px;
    align-items: center;
    background-color: rgb(67, 245, 44);
    font-family: fantasy;
    color: rgb(18, 37, 29);
    justify-content: center;
    height: 50px;
    width: calc(100% - 140px);
    position: relative;
    display: flex;
    margin: 10px;
    transform: translate(10px, 10%);
    font-size: 150%;
}

@keyframes anim{
    0% {color: rgb(231, 231, 209);} 
   25% {color: rgb(111, 240, 60);} 
   50% {color: #000;} 
   75% {color: rgb(212, 81, 30);} 
  100% {color: rgb(236, 215, 96);} 
}

.IconoFlecha {
    border: blueviolet;
    border-radius: 50%;
    cursor: pointer;
    font-size: 50px;
    position: fixed;
    bottom: 24px;
    right: 91px;
    font-weight: lighter;
    color: rgb(28, 226, 28);
    display: inline-table;
    justify-content: flex-start;
    align-items: center;
    height: 64px;
    width: 120px;
    animation-duration: 0.5s;
    animation-name: anim;
  animation-iteration-count: infinite;
    transform: rotate(0.5 turn);
    transition: .3 ease;
}