input {
    border-radius: 5px;
    background-color: #526E48;
    align-items: center;
    align-content: center;
    font-family: fantasy;
    font-weight: 400;
    height: 64px;
    color: #d0f0cf;
    font-style: inherit;
    font-size: 24px;
    box-shadow: black;
    width: calc(100% - 140px);
    text-align: center;
    margin: 30px;
}

