.CargaTareas {
    border-radius: 5px;
    align-items: center;
    background-color: rgb(65, 228, 127);
    font-family: fantasy;
    color: rgb(5, 41, 15);
    justify-content: center;
    height: 50px;
    width: calc(100% - 140px);
    position: relative;
    display: flex;
    margin: 10px;
    transform: translate(10px, 10%);
    font-size: 150%;
}
