.TodoItem {
    border-radius: 5px;
    background-color: #9EDF9C;
    font-family: fantasy;
    color: #526E48;
    justify-content: center;
    height: 50px;
    width: calc(100% - 140px);
    position: relative;
    display: flex;
    margin: 10px;
}

.TodoItem-p {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 1px;
    font-weight: 400;
}

.TodoItem-p--complete {
    text-decoration: line-through;
}

.Icon {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
    font-weight: bold;

}

.Icon-check {
    position: center;
    left:12px;
}

.Icon-check:hover{
    color:rgb(25, 120, 175);
}

.Icon-check--active {
    color: rgb(44, 112, 61);
}

.Icon-delete {
    position: absolute;
    top: 0px;
    right: 0;
    color:crimson;
}

.Icon-edit {
    position: absolute;
    top: 0px;
    right: 45px;
    color:gray;
}

.Icon-edit:hover {
    color : black;
}

.Icon-delete:hover {
    color: red;
}