.TodoCounter {
    font-size: ' 24px' ;
    text-align: center;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    margin: 0;
    padding: '48px';
    color: azure;
    background-color: #9EDF9C;
    border-radius: 5%;
    height: 100%;
    width: calc(100% - 120px);
    font-weight: normal;
    margin: 24px;

    
}

.TodoCounter span {
   font-weight: bold;
}

.TodoCounter--loading {
    opacity: 15%;
}
